.barbecueInstructionTitle {
  color: #771b0f;
  font-size: 20px;
  margin-bottom: 5px;
}

div.barbecueLine {
  text-align: center;
  width: 60%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.barbecueSteps div.step1,
div.barbecueSteps div.step2,
div.barbecueSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #771b0f;
}

@media (min-width: 600px) {
  .barbecueInstructionTitle {
    font-size: 35px;
  }
  div.barbecueLine {
    height: 20px;
  }
}
