div.barbecueInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
}

img.barbecueArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
}

.barbecueNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.barbecueTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.barbecueLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 15px;
}
div.barbecueLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

div.barbecueNutritionalValue {
  display: flex;
}

div.barbecueNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.barbecueAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.barbecueLineInformations {
    width: 310px;
  }
  div.barbecueLineAlergens {
    width: 140px;
  }
  p.barbecueTitle {
    font-size: 35px;
  }
  div.barbecueNutritionalValue,
  .barbecueAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.barbecueArrow {
    margin: 20px auto;
  }
  div.barbecueInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.barbecueTitle {
    font-size: 23px;
    color: #931237;
    font-weight: 700;
  }
  div.barbecueLineInformations {
    width: 240px;
    height: 12px;
    background-size: contain;
    background-position: center;
  }

  div.barbecueLineAlergens {
    width: 120px;
    height: 11px;
    background-position: center;
  }
  .barbecueNutritionalValues {
    color: #931237;
    transition: 0.1s;
  }

  div.barbecueNutritionalValue,
  .barbecueAlergensDescription {
    font-size: 12px;
  }

  .barbecueAlergensDescription {
    color: #931237;
    display: block;
  }
}
