div.hamChorizoTopBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/background-top-hamChorizo.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

div.hamChorizo {
  margin-left: 6%;
  text-align: center;
  position: absolute;
  top: 8%;
  width: 50%;

}

div.hamChorizoFarm {
  margin-left: 58%;
  text-align: center;
  position: absolute;
  top: 6%;
  width: 40%;
}

p.hamChorizoDescription {
  position: absolute;
  top: 100%;
  color: #931237;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 50px;
}

.chorizoPizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 99.8%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (min-width: 320px) {
  div.hamChorizoTopBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.hamChorizoDescription {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.hamChorizoDescription {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 80px;
  }
}
@media (min-width: 550px) {
  p.hamChorizoDescription {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.hamChorizoDescription {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .chorizoPizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.hamChorizoTopBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.hamChorizo {
    text-align: left;
    top: 110px;
    margin-left: 12%;
    width: 25%;
  }

  div.hamChorizoFarm {
    display: block;
    margin-left: 40%;
    top: 90px;
    width: 20%;
  }

  p.hamChorizoDescription {
    color: #fffad4;
    top: 25%;
    left: 0;
    font-size: 16px;
    width: 38%;
  }

  div.ingredients_desktop img.desktop_hamChorizoIngredients {
    width: 20%;
    bottom: 0%;
    left: 12%;
  }
}
@media (min-width: 1200px) {
  p.hamChorizoDescription {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.hamChorizoDescription {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.hamChorizoDescription {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.hamChorizoDescription {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.hamChorizoDescription {
    font-size: 32px;
  }
}
